<template>
	<div class="order container">
		<header>
			<i class="iconfont icon-fanhui" @click="$router.back()"></i>
				<span>提交订单</span>
			<i class="iconfont icon-kefu"></i>
		</header>
		<section>
			<div class="address" @click="goAddress">
				<h3 class="address-title">收货信息</h3>
				<div class="address-content">
					<div>
						<span>{{path.name}}</span>
						<span>{{path.tel}}</span>
					</div>
					<div>
						<span>{{path.province}}</span>
						<span>{{path.city}}</span>
						<span>{{path.county}}</span>
						<span>{{path.addressDetail}}</span>
					</div>
				</div>
			</div>
			<div class="payment">
				<div class="payment-title">支付方式：</div>
				<van-radio-group v-model="checkedPayment">
				  <van-radio name="wx">微信支付</van-radio>
				  <van-radio name="ali">支付宝支付</van-radio>
				</van-radio-group>
			</div>
			<div class="goods">
				<ul>
					<li 
						v-for="(item,index) in goodsList"
						:key="index">
						 <div>
							<img :src="item.goods_imgUrl" alt="">
						</div>
						<div class="goods-content">
							<h4>{{item.goods_name}}</h4>
							<div class="goods-price">
								<span>￥{{item.goods_price}}</span>
								<span>x{{item.goods_num}}</span>
							</div>
							
						</div>
					</li>
				</ul>
			</div>
		</section>
		<footer>
			<div class="order-total">
				<span>共</span>
				<b>{{total.num}}</b>
				<span>件,</span>
				<span>总金额：</span>
				<em>￥{{total.price}}</em>
			</div>
			<div class="order-topay" @click="goPayment">提交订单</div>
		</footer>
	</div>
</template>

<script>
import {mapState,mapGetters,mapMutations,mapActions} from 'vuex'
import http from '@/common/api/request.js'
import bus from '@/common/bus.js'
export default{
	data(){
		return{
			checkedPayment:'wx',
			path:{},
			item:[]
		}
	},
	computed:{
		...mapState({
			list:state=>state.cart.list,
		}),
		...mapGetters(['total','defaultPath']),
		goodsList(){
			return this.item.map(id=>{
				console.log(id)
				return this.list.find(v => v.id == id)
			})
		}
	},
	created() {
		// 选中商品
		this.item = JSON.parse( this.$route.query.detail );
		http.$axios({
			url:'/api/selectAddress',
			method:'post',
			headers:{
				token:true
			}
		}).then(res=>{
			this.initData(res.data)
			if(this.defaultPath.length){
				this.path = this.defaultPath[0]
			}else{
				this.path = res.data[0]
			}
			// console.log(res.data)
		})
		this.goodsList = JSON.parse( this.$route.query.goodsList );
		this.selectAddress();
	},
   activated(){
		bus.$on('selectPath', function(data){
			 
			this.path = JSON.parse(data);
			 
		}.bind(this));
		//选中的商品id号
		this.item = JSON.parse( this.$route.query.detail );
		this.goodsList = JSON.parse( this.$route.query.goodsList );
		this.selectOrder();
	},
	methods:{
		...mapMutations(['initData']),
		 //查询地址
		selectAddress(){
			http.$axios({
				url:'/api/selectAddress',
				method:"post",
				headers:{
					token:true
				}
			}).then(res=>{
				this.initData( res.data );
				//有默认收货地址
				if( this.defaultPath.length ){
					this.path = this.defaultPath[0];
				}else{
					this.path = res.data[0];
				}
			})
		},
		 //查询订单
		selectOrder(){
			http.$axios({
				url:'/api/selectOrder',
				method:"post",
				headers:{
					token:true
				},
				data:{
					orderId:this.order_id
				}
			}).then(res=>{
				this.initOrder(res.data);
				
				this.total = {
					price : res.data[0].goods_price,
					num : res.data[0].goods_num
				}
			})
		},
		// 提交订单
		goPayment(){
			this.$router.push({
				path:'/pay',	
			})
		},
		// 选择收货地址
		goAddress(){
			this.$router.push({
				path:'/address',
				query:{
					type:'select'
				}
			})
		}
	}
}
</script>

<style scoped>
	header{
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 1.173333rem;
		background-color: darkgreen;
		color: #fff;
	}
	header i{
		padding:0 0.4rem;
		font-size:0.586666rem;
	}
	header span{
		font-weight: 400;
		font-size:0.48rem;
	}
	section{
		background-color: #f7f7f7;
	}
	.address-title{
		padding: 0.4rem;
		font-size:0.48rem;
	}
	.address-content{
		padding:0.16rem 0.4rem;
		font-size:0.373333rem;
		background-color: #fff;
	}
	.address-content span{
		padding-right:0.16rem;
	}
	.payment{
		padding:0.16rem 0.4rem;
		margin-top:0.4rem;
		font-size:0.426666rem;
		background-color: #fff;
	}
	.van-radio-group{
		padding:0.16rem 0;
		display: flex;
	}
	.van-radio{
		padding-right:0.266666rem;
	}
	.goods{
		padding:0.16rem 0.4rem;
		margin-top:0.4rem;
		font-size:0.426666rem;
		background-color: #fff;
	}
	.goods ul{
		width: 100%;
	}
	.goods ul li{
		display: flex;
		width: 100%;
	}
	.goods ul li img{
		width: 1.973333rem;
		height: 1.973333rem;
	}
	.goods-content{
		display: flex;
		flex: 1;
		flex-direction: column;
		justify-content: space-between;
		padding-left:0.4rem;
	}
	.goods-price{
		display: flex;
		justify-content: space-between;
	}
	footer{
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 1.28rem;
		border-top:0.053333rem solid #ccc;
	}
	.order-total{
		font-size:0.426666rem;
	}
	.order-total span{
		padding:0 0.16rem;
	}
	.order-total b{
		color: #b0352f;
	}
	.order-total em{
		font-size:0.48rem;
		color: #b0352f;
	}
	.order-topay{
		width: 3.2rem;
		line-height: 1.2rem;
		font-size:0.426666rem;
		color: #fff;
		text-align: center;
		background-color: darkgreen;
	}
</style>